import createDebug from 'debug';

type DebugLogger = (...args: unknown[]) => void;

/** ç¨æ¥ä½ä¸ºå¼å³ */
const debugNoStackTraceSwitch: (...args: unknown[]) => void = createDebug('Timeline:notrace');

function logWithStackTrace(logger: DebugLogger) {
  if (!debugNoStackTraceSwitch.enabled) {
    // eslint-disable-next-line
    logger.log = console.trace.bind(console);
  }

  return logger;
}

/** è°è¯ä¸»ç¨åº */
export const debug = logWithStackTrace(createDebug('Timeline'));

export default debug;
