import { type Point } from './PointScale';
// eslint-disable-next-line import/no-named-default
import type { default as Timeline, TimeValue, TimeDataItem } from '../Timeline';
import { EVENT_TYPE_MAP } from './EventHandler';

/** æ°æ®é¡¹ */
export type DataItem = Pick<Point, 'type' | 'index'> & {
  time: string;
  originTime: TimeValue;
};

/**
 * æ°æ®éç®¡ç
 */
export default class Dataset {
  /** æ¶é´è½´å®ä¾ */
  timeline: Timeline;

  /** æ¶é´æ°æ® */
  protected _timeData: TimeDataItem[] = [];

  /** è§£æåçæ°æ® */
  protected _data: DataItem[] = [];

  /** æ°æ®é¡¹ææç´¢å¼åºé´ï¼æ­æ¾åºé´ï¼ å¼é­ [,) */
  protected _indexRange: [number, number] = [0, 0];

  /** åºé´å¼å³ */
  protected _isRange = false;

  /** å½åæ°æ®é¡¹ç´¢å¼ */
  protected _currentIndex = 0;

  constructor(timeline: Timeline, timeData: TimeDataItem[], indexRange?: [number, number]) {
    this.timeline = timeline;
    this.__verify(timeData);
    // deepClone
    this._timeData = [...timeData];
    if (typeof indexRange !== 'undefined') {
      this._indexRange = indexRange.slice() as [number, number];
      this._isRange = true;
    } else {
      this._indexRange = [0, this._timeData.length];
    }
  }

  /**
   * éªè¯æ°æ®åæ³æ§
   */
  private __verify(timeData: TimeDataItem[]) {
    if (
      !timeData ||
      timeData.length <= 0 ||
      timeData[0] === null ||
      typeof timeData[0] === 'undefined'
    ) {
      throw new Error(`invalid data: ${JSON.stringify(timeData)}`);
    }

    const isObj =
      typeof timeData[0] === 'object' && timeData[0] !== null && !Array.isArray(timeData[0]);

    if (isObj && (timeData[0].type !== 'main' || timeData[timeData.length - 1].type !== 'main')) {
      throw new Error('The first and last data must be "main" type.');
    }

    return this;
  }

  /**
   * è§£ææ°æ®
   */
  parse() {
    const self = this;

    const isObj =
      typeof self._timeData[0] === 'object' &&
      self._timeData[0] !== null &&
      !Array.isArray(self._timeData[0]);

    this._data = self._timeData.map((item, index) => {
      // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
      const originTime: DataItem['originTime'] = isObj ? item.value : item;
      const time = String(originTime);

      if (
        Number.isNaN(Number(originTime)) &&
        typeof originTime !== 'string' &&
        !(originTime instanceof String)
      ) {
        throw new Error(`wrong time format: ${originTime}, should be number or string.`);
      }

      if (isObj) {
        return {
          index,
          type: item.type as Point['type'],
          time,
          originTime
        };
      }

      return {
        index,
        type: 'main',
        time,
        originTime
      };
    });

    this.__setIndexRange(this._indexRange.slice() as [number, number]);
  }

  /**
   * è·åæ°æ®é
   */
  getData(range = true) {
    if (range === true) {
      // deepClone
      return this._data.slice(...this._indexRange);
    }

    // deepClone
    return this._data.slice();
  }

  /**
   * æ ¹æ®ç´¢å¼è·åæ°æ®é¡¹
   */
  getDataItem(index: number) {
    return this._data[index];
  }

  /**
   * æ¯å¦ææçç´¢å¼
   */
  isValidIndex(index: number) {
    return typeof this._data[index] !== 'undefined';
  }

  // eslint-disable-next-line class-methods-use-this
  getMinIndex(range = true) {
    if (range === true) {
      return this._indexRange[0];
    }

    return 0;
  }

  getMaxIndex(range = true) {
    if (range === true) {
      return this._indexRange[1] - 1;
    }

    return this._data.length - 1;
  }

  /**
   * æ¯å¦å¨æå°ç´¢å¼å¤
   */
  isIndexMin(index = this._currentIndex, range = true) {
    return index <= this.getMinIndex(range);
  }

  /**
   * æ¯å¦å¨æå¤§ç´¢å¼å¤
   */
  isIndexMax(index = this._currentIndex, range = true) {
    return index >= this.getMaxIndex(range);
  }

  /**
   * è·åå½åæ°æ®ç´¢å¼
   */
  getCurrentIndex() {
    return this._currentIndex;
  }

  /**
   * è®¾ç½®å½åæ°æ®ç´¢å¼
   * ! éå¶å¨ IndexRange åºé´ä¸­
   */
  private __setCurrentIndex(index: number) {
    this._currentIndex = Math.min(Math.max(this.getMinIndex(true), index), this.getMaxIndex(true));
  }

  /**
   * æ¯å¦æåºé´éå¶
   */
  isRange() {
    return this._isRange;
  }

  /**
   * è·åæ°æ®ç´¢å¼åºé´, å¼é­ [,)
   */
  getIndexRange(): [number, number] {
    // deepClone
    return [...this._indexRange];
  }

  /**
   * è®¾ç½®æ°æ®ç´¢å¼åºé´, å¼é­ [,)
   */
  private __setIndexRange(indexRange: [number, number]) {
    if (!this.isRange()) {
      return;
    }
    if (indexRange[0] > indexRange[1]) {
      throw new Error(`indexRange[1] must be greater than indexRange[0].`);
    }

    // åç¡®å®ä¸é
    this._indexRange[1] = Math.min(
      Math.max(this.getMinIndex(false), indexRange[1]),
      this.getMaxIndex(false) + 1
    );

    this._indexRange[0] = Math.min(
      Math.max(this.getMinIndex(false), indexRange[0]),
      this._indexRange[1]
    );
  }

  /**
   * æ¯å¦æ¯èå´åçææç´¢å¼
   */
  isInRangeIndex(index: number) {
    return this.isValidIndex(index) && this.getMinIndex() <= index && index <= this.getMaxIndex();
  }

  /**
   * æ´æ°ç¶æ
   */
  update({ currentIndex, indexRange }: { currentIndex?: number; indexRange?: [number, number] }) {
    //
    if (typeof indexRange !== 'undefined') {
      this.__setIndexRange(indexRange);
    }
    if (typeof currentIndex !== 'undefined') {
      this.__setCurrentIndex(currentIndex);
    }

    if (this.isRange() && typeof indexRange !== 'undefined') {
      // @ts-ignore
      this.timeline._dispatchEvent(EVENT_TYPE_MAP.CHANGE_RANGE, {
        index: this.getCurrentIndex(),
        indexRange: this.getIndexRange()
      });
    }
  }

  //
}
